import React, { useState } from 'react'

const AddTodo = ({saveTodo}) => {
  const [value, setValue] = useState('');
  // è¿™æ˜¯ä¸€ä¸ªå—æ§è¡¨å•ç»„ä»¶
  return (
    <form
      onSubmit={e => {
        e.preventDefault()
        saveTodo(value)
        setValue('')
      }}
    >
      <input
        placeholder="Add todo"
        onChange={event => {
          setValue(event.target.value);
        }}
        value={value}
      />
    </form>
  )
}

export default AddTodo
