import React, { useEffect, useState } from 'react';
import { Table } from 'antd';
import api from '../service/api';

const columns = [
  {
    title: 'æ’å',
    dataIndex: 'rank',
    width: '50%',
  },
  {
    title: 'åˆ†æ•°',
    dataIndex: 'score',
  },
];

export default function StateHookTable(props) {
  // æ•°æ®åˆ—è¡¨
  const [data, setData] = useState([]);
  // åˆ†é¡µæ•°æ®
  const [pagination, setPagination] = useState({
    total: 0,  // æ•°æ®æ€»æ¡æ•°
    pageSize: 10,  // é¡µé¢æ˜¾ç¤ºæ¡æ•°
    showSizeChanger: true,  // æ˜¾ç¤ºpageSizeæ”¹å˜æŒ‰é’®
    current: 1, // å½“å‰é¡µç 
    loading: false, // åŠ è½½çŠ¶æ€
  });

  // è¯·æ±‚æ•°æ®
  async function getRankData() {
    const { pageSize, current } = pagination;
    const offset = (current - 1) * pageSize;
    setPagination({
      ...pagination,
      loading: true,
    });
    const res = await api.getRank(offset, pageSize);
    setData(res.results);
    setPagination({
      ...pagination,
      total: res.total,
      loading: false,
    });
  }

  // åªæœ‰[pagination.pageSize, pagination.current] æ”¹å˜çš„æ—¶å€™æ‰§è¡Œ
  useEffect(() => {
    getRankData();
  }, [pagination.pageSize, pagination.current]);

  const handleTableChange = (tablePagination, filters, sorter) => {
    setPagination({
      ...pagination,
      ...tablePagination,
    });
  };

  return (
    <React.Fragment>
      <h3>ä½¿ç”¨state hookså®ç°çš„è¡¨æ ¼</h3>
      <Table
        columns={columns}
        rowKey={record => record.id}
        dataSource={data}
        pagination={pagination}
        loading={pagination.loading}
        onChange={handleTableChange}
      />
    </React.Fragment>
  );
}

