import React, { Component } from 'react';
import { Table } from 'antd';
import api from '../service/api';

const columns = [
  {
    title: 'æ’å',
    dataIndex: 'rank',
    width: '50%',
  },
  {
    title: 'åˆ†æ•°',
    dataIndex: 'score',
  },
];

export default class StateTable extends Component {
  state = {
    data: [], // æ•°æ®åˆ—è¡¨
    pagination: {
      total: 0, // æ•°æ®æ€»æ¡æ•°
      pageSize: 10, // æ¯é¡µæ•°é‡
      showSizeChanger: true, // æ˜¾ç¤ºpageSizeæ”¹å˜æŒ‰é’®
    },
    loading: false, // åŠ è½½çŠ¶æ€
  };

  componentDidMount() {
    this.getRankData(1);
  }

  async getRankData(page) {
    const { pagination } = this.state;
    // æ•°æ®æ¡æ•°
    const { pageSize } = pagination;
    // æ•°æ®èµ·å§‹index
    const offset = (page - 1) * pageSize;
    // å±•ç¤ºåŠ è½½ing UI
    this.setState({ loading: true });
    // è¯·æ±‚æ•°æ®
    const data = await api.getRank(offset, pageSize);
    // å­˜åˆ°stateä¸­
    this.setState((state, props) => {
      const pager = { ...state.pagination };
      pager.total = data.total;
      return {
        pagination: pager, // åˆ†é¡µæ•°æ®
        data: data.results, // æ•°æ®åˆ—è¡¨
        loading: false, // åŠ è½½çŠ¶æ€
      };
    });
  }

  // ç¿»é¡µäº‹ä»¶
  handleTableChange = (pagination, filters, sorter) => {
    // æ”¹å˜æ•°æ®
    this.setState(
      (state, props) => {
        const pager = { ...state.pagination, ...pagination };
        return {
          pagination: pager,
        };
      },
      // stateæ”¹å˜æˆåŠŸä¹‹åå»è·å–æ•°æ®
      () => this.getRankData(pagination.current)
    );
  };

  render() {
    const { data, pagination, loading } = this.state;
    return (
      <React.Fragment>
        <h3>ä½¿ç”¨classç»„ä»¶å®ç°çš„è¡¨æ ¼</h3>
        <Table
          columns={columns}
          rowKey={record => record.id}
          dataSource={data}
          pagination={pagination}
          loading={loading}
          onChange={this.handleTableChange}
        />
      </React.Fragment>
    );
  }
}
